footer{
    @include component-size;
    margin: 30px auto;
    p{
        margin-top: 50px;
    }
    a{
        color: inherit;
    }
    .f-menu{
        margin: 30px 0;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
        @include responsive($breakpoint-medium){
            grid-template-columns: repeat(2, 1fr);
        }
        @include responsive($breakpoint-small){
            grid-template-columns: repeat(1, 1fr);
        }
    }
}